<template>

      <div class="box">

        <h3>İzinli Ekle</h3>
      <form @submit.prevent="onFormSubmit">
        
          <input
            type="text"
            class="form-control"
            placeholder="Talebe İsmi"
            v-model="izinli.izinli_isim"
            required
          />
     
        <div class="form-check-inline">
          <input
            type="radio"
            name="izinli_grup"
            value="A"
            v-model="izinli.izinli_grup"
            checked
            required
          />
          <label for="inlineRadio1">A Grubu</label>
        </div>

        <div class="form-check-inline">
          <input
            type="radio"
            name="izinli_grup"
            value="B"
            v-model="izinli.izinli_grup"
            required
          />
          <label for="inlineRadio2">B Grubu</label>
        </div>
          
          <input
            type="text"
            class="form-control"
            placeholder="İzin Nevisi"
            v-model="izinli.izinli_nevi"
            required
          />
        
          <input
            type="time"
            class="form-control"
            v-model="izinli.izinli_cikis_saati"
            required
          />
        
          <input
            type="time"
            class="form-control"
            v-model="izinli.izinli_gelis_saati"
          />

 <button class="btn new-add-save">Kaydet</button>



      </form>
    </div>
  
</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      izinli: {},
    };
  },
  methods: {
    onFormSubmit(event) {
      event.preventDefault();
      db.collection("izinliler")
        .add(this.izinli)
        .then(() => {
          // alert("User successfully created!");
          this.izinli.izinli_isim = "";
          this.izinli.izinli_grup = "";
          this.izinli.izinli_nevi = "";
          this.izinli.izinli_cikis_saati = "";
          this.izinli.izinli_gelis_saati = "";
          this.$router.push("/");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

